export const numberPrettify = (value: number | string) =>
  new Intl.NumberFormat("ru-RU").format(
    typeof value === "string"
      ? +value.replaceAll(/\s/g, "").replace(",", ".")
      : value
  );
export const numberPrettifyWithDigits = (
  value: number | string,
  digits = 0
) => {
  return new Intl.NumberFormat("ru-RU", {
    minimumFractionDigits: digits,
    maximumFractionDigits: digits,
  })
    .format(
      typeof value === "string"
        ? +value.replaceAll(/\s/g, "").replace(",", ".")
        : value
    )
    .replace(",", ".");
};
