import { getHttp } from "@/kernel/api/http";
import { ApiResponseType, ApiRequestListOptionsType } from "@/kernel/api/types";
import { NotRequestException } from "@/kernel/lib/exceptions/not-request.exception";
import {
  OfferAddData,
  OfferCalcData,
  OfferCalcParamsData,
  OfferGetData,
  OfferEditData,
} from "@/kernel/api/dto/Offer";
import { ApiBackendSearchParamsType } from "@/kernel/lib/ApiBackend/ApiBackend";

export const getOfferCalc = async (
  payload: ApiBackendSearchParamsType
): Promise<ApiResponseType<OfferCalcData>> => {
  const res = await getHttp().get<ApiResponseType<OfferCalcData> | false>(
    "/offer/calc",
    {
      searchParams: payload,
    }
  );

  if (!res) {
    throw new NotRequestException(getOfferCalc.name);
  }

  return res;
};

export const getOfferDelete = async (id: number): Promise<ApiResponseType> => {
  const res = await getHttp().get<ApiResponseType | false>("/offer/delete", {
    searchParams: {
      offer_id: id,
    },
  });

  if (!res) {
    throw new NotRequestException(getOfferDelete.name);
  }

  return res;
};

export const getOfferGet = async (
  id: number
): Promise<ApiResponseType<OfferGetData>> => {
  const res = await getHttp().get<ApiResponseType<OfferGetData> | false>(
    "/offer/get",
    {
      searchParams: {
        offer_id: id,
      },
    }
  );

  if (!res) {
    throw new NotRequestException(getOfferGet.name);
  }

  return res;
};

export const getOfferList = async (
  params?: ApiRequestListOptionsType
): Promise<ApiResponseType<OfferGetData[]>> => {
  const res = await getHttp().get<ApiResponseType<OfferGetData[]> | false>(
    "/offer/list",
    { searchParams: { ...params } }
  );

  if (!res) {
    throw new NotRequestException(getOfferList.name);
  }

  return res;
};

export const postOfferAdd = async (
  payload: OfferAddData
): Promise<ApiResponseType> => {
  const res = await getHttp().post<ApiResponseType | false>("/offer/add", {
    ...payload,
    param: JSON.stringify(payload.param),
  });

  if (!res) {
    throw new NotRequestException(postOfferAdd.name);
  }

  return res;
};

export const postOfferEdit = async (
  payload: OfferEditData
): Promise<ApiResponseType> => {
  const res = await getHttp().post<ApiResponseType | false>("/offer/edit", {
    ...payload,
    param: JSON.stringify(payload.param),
  });

  if (!res) {
    throw new NotRequestException(postOfferEdit.name);
  }

  return res;
};
